html {
	font-family: sans-serif;
	line-height: 1.15;
	-ms-text-size-adjust: 100%;
	-webkit-text-size-adjust: 100%;
}

body {
	margin: 0;
	padding: 0;
}

article, aside, footer, header, nav, section {
	display: block;
}

h1 {
	font-size: 2em;
	margin: 0.67em 0;
}

figcaption, figure, main {
	display: block;
}

figure {
	margin: 1em 40px;
}

hr {
	box-sizing: content-box;
	height: 0;
	overflow: visible;
}

pre {
	font-family: monospace, monospace;
	font-size: 1em;
}

a {
	background-color: transparent;
	-webkit-text-decoration-skip: objects;
}

a:active, a:hover {
	outline-width: 0;
}

abbr[title] {
	border-bottom: none;
	text-decoration: underline;
	text-decoration: underline dotted;
}

b, strong {
	font-weight: inherit;
}

b, strong {
	font-weight: bolder;
}

code, kbd, samp {
	font-family: monospace, monospace;
	font-size: 1em;
}

dfn {
	font-style: italic;
}

mark {
	background-color: #ff0;
	color: #000;
}

small {
	font-size: 80%;
}

sub, sup {
	font-size: 75%;
	line-height: 0;
	position: relative;
	vertical-align: baseline;
}

sub {
	bottom: -0.25em;
}

sup {
	top: -0.5em;
}

audio, video {
	display: inline-block;
}

audio:not([controls]) {
	display: none;
	height: 0;
}

img {
	border-style: none;
}

svg:not(:root) {
	overflow: hidden;
}

button, input, optgroup, select, textarea {
	font-family: sans-serif;
	font-size: 100%;
	line-height: 1.15;
	margin: 0;
}

button, input {
	overflow: visible;
}

button, select {
	text-transform: none;
}

button, html [type="button"], [type="reset"], [type="submit"] {
	-webkit-appearance: button;
}

button::-moz-focus-inner, [type="button"]::-moz-focus-inner, [type="reset"]::-moz-focus-inner, [type="submit"]::-moz-focus-inner {
	border-style: none;
	padding: 0;
}

button:-moz-focusring, [type="button"]:-moz-focusring, [type="reset"]:-moz-focusring, [type="submit"]:-moz-focusring {
	outline: 1px dotted ButtonText;
}

fieldset {
	border: 1px solid #c0c0c0;
	margin: 0 2px;
	padding: 0.35em 0.625em 0.75em;
}

legend {
	box-sizing: border-box;
	color: inherit;
	display: table;
	max-width: 100%;
	padding: 0;
	white-space: normal;
}

progress {
	display: inline-block;
	vertical-align: baseline;
}

textarea {
	overflow: auto;
}

[type="checkbox"], [type="radio"] {
	box-sizing: border-box;
	padding: 0;
}

[type="number"]::-webkit-inner-spin-button, [type="number"]::-webkit-outer-spin-button {
	height: auto;
}

[type="search"] {
	-webkit-appearance: textfield;
	outline-offset: -2px;
}

[type="search"]::-webkit-search-cancel-button, [type="search"]::-webkit-search-decoration {
	-webkit-appearance: none;
}

::-webkit-file-upload-button {
	-webkit-appearance: button;
	font: inherit;
}

details, menu {
	display: block;
}

summary {
	display: list-item;
}

canvas {
	display: inline-block;
}

template, [hidden] {
	display: none;
}

html {
	box-sizing: border-box;
}

*, *::before, *::after {
	box-sizing: inherit;
}

html, body {
	height: 100%;
}

body {
	font-family: "Open Sans", sans-serif;
	font-size: 16px;
	line-height: 1;
	color: #000;
	overflow-x: hidden;
	background-color: #f3f2ed;
}

::selection {
	background: #b3d4fc;
	text-shadow: none;
}

img {
	display: block;
	max-width: 100%;
	height: auto;
}

h1, h2, h3, h4, h5, h6 {
	margin: 0;
}

p {
	margin: 0;
}

ul, ol {
	margin: 0;
	padding: 0;
}

ul li {
	list-style: none;
}

textarea {
	resize: none;
}

.container {
	max-width: 37.5rem;
	margin: 0 auto;
}

@media only screen and (max-width: 640px) {
	.container {
		margin: 0 20px;
	}
}

.container-inner {
	max-width: 33.75rem;
	margin: 0 auto;
}

.header .container {
	background-color: #343538;
}

.header .container::after {
	content: "";
	display: block;
	clear: both;
}

.header .container-inner {
	min-height: 5.5rem;
	padding-top: 1.6875rem;
}

.header .container-inner::after {
	content: "";
	display: block;
	clear: both;
}

.header__logo {
	float: left;
}

@media only screen and (max-width: 480px) {
	.header__logo {
		float: none;
		text-align: center;
	}
}

.header__trouble {
	float: right;
}

@media only screen and (max-width: 480px) {
	.header__trouble {
		float: none;
		text-align: center;
	}
}

.logo__link {
	font-size: 2.625em;
	font-weight: 700;
	color: #fff;
	text-decoration: none;
	text-transform: uppercase;
}

.trouble__text {
	font-size: 0.8125em;
	line-height: 4.6;
	font-weight: 400;
	color: #b3b3b3;
}

.trouble__link {
	color: #fff;
	text-decoration: none;
	transition: .25s linear;
}

.trouble__link:hover {
	color: #ccc;
}

.pretty-image__pic {
	height: 17.5rem;
	background-position: 50% 0;
	background-repeat: no-repeat;
	background-size: cover;
}

.default-section {
	margin-top: 0.9375rem;
	background-color: #fff;
	padding: 1.75rem 0;
}

.default-section_dark {
	background-color: #343538;
	min-height: 7.5rem;
}

.container .default-section:first-child {
	margin-top: 0;
}

.blog {
	text-align: left;
}

@media only screen and (max-width: 480px) {
	.blog {
		text-align: center;
	}
}

.glob-title {
	font-size: 1.125em;
	font-weight: 600;
	color: #343538;
	margin-bottom: 1.25rem;
}

.glob-text {
	font-size: 0.875em;
	line-height: 1.8;
	font-weight: 400;
	color: #a7a7a7;
	margin-bottom: 1.25rem;
}

.button {
	display: inline-block;
	text-align: center;
	max-width: 6.25rem;
	width: 100%;
	height: 2.25rem;
	line-height: 2.25rem;
	background-color: #343538;
	border-radius: 25px;
	color: #fff;
	text-decoration: none;
	transition: .5s linear;
	cursor: pointer;
}

.button:hover {
	background-color: #111;
}

.advantages {
	text-align: center;
}

.advantage {
	display: inline-block;
	max-width: 8.75rem;
	width: 100%;
	margin-right: 2.5rem;
}

.advantage:nth-child(3n) {
	margin-right: 0;
}

@media only screen and (max-width: 550px) {
	.advantage {
		display: block;
		margin: 0 auto;
	}
	.advantage:nth-child(3n) {
		margin-right: auto;
	}
}

.advantage__title {
	position: relative;
	padding-top: 4.6875rem;
}

.advantage__title:before {
	content: '';
	position: absolute;
	left: 50%;
	top: 0;
	transform: translate(-50%, 0);
}

.advantage__title.gear:before {
	background-image: url(../img/sprite/sprite.png);
	background-position: 0px 0px;
	width: 50px;
	height: 51px;
}

.advantage__title.mail:before {
	background-image: url(../img/sprite/sprite.png);
	background-position: -117px 0px;
	width: 47px;
	height: 47px;
}

.advantage__title.tool:before {
	background-image: url(../img/sprite/sprite.png);
	background-position: -60px 0px;
	width: 47px;
	height: 48px;
}

.twice::after {
	content: "";
	display: block;
	clear: both;
}

.twice__text {
	width: 50%;
	float: left;
	padding: 0 5%;
}

.twice__img {
	min-height: 12.5rem;
	max-height: 12.5rem;
	background-repeat: no-repeat;
	background-position: 50% 50%;
	background-size: cover;
	width: 50%;
	float: left;
}

.work {
	text-align: center;
}

.work__items::after {
	content: "";
	display: block;
	clear: both;
}

.work__item {
	min-height: 10.9375rem;
	max-height: 10.9375rem;
	background-repeat: no-repeat;
	background-position: 50% 50%;
	background-size: cover;
	width: 33.33%;
	float: left;
	position: relative;
}

@media only screen and (max-width: 640px) {
	.work__item {
		width: 50%;
	}
}

@media only screen and (max-width: 500px) {
	.work__item {
		min-height: 7.8125rem;
		max-height: 7.8125rem;
	}
}

.work__item:before {
	position: absolute;
	left: 0;
	top: 0;
	width: 100%;
	height: 100%;
	content: '';
	background-color: rgba(255, 255, 255, 0.25);
}

.work__item:hover:before {
	display: none;
}

.sale {
	text-align: center;
	font-weight: 600;
	color: #fff;
	line-height: 2;
}

.more-blog__text {
	margin-bottom: 0;
}

.check {
	text-align: center;
}

.check__img {
	height: 15rem;
	background-repeat: no-repeat;
	background-position: 50% 0;
	background-size: cover;
}

@media only screen and (max-width: 640px) {
	.check__img {
		max-width: 20rem;
		margin: 0 auto;
	}
}

.footer .container {
	background-color: #343538;
	min-height: 11.25rem;
}

.footer {
	text-align: center;
	margin-top: 0.9375rem;
}

.footer__social {
	max-width: 25rem;
	width: 100%;
	margin: 0 auto;
	padding: 1.5625rem 0;
	text-align: center;
}

@media only screen and (max-width: 360px) {
	.footer__social {
		max-width: 12.5rem;
	}
}

.footer__item {
	display: inline-block;
	margin-right: 0.4375rem;
	margin-bottom: 0.125rem;
}

@media only screen and (max-width: 440px) {
	.footer__item {
		margin-right: 0;
	}
}

.footer__item:last-child {
	margin-right: 0;
}

.footer__soc {
	display: block;
	width: 2.1875rem;
	height: 2.1875rem;
	background-color: #fff;
	border-radius: 50%;
	transition: .5s linear;
	position: relative;
}

.footer__soc:before {
	position: absolute;
	left: 50%;
	top: 50%;
	transform: translate(-50%, -50%);
	content: '';
}

.footer__soc:hover {
	background-color: rgba(255, 255, 255, 0.35);
	box-shadow: inset 0 0 10px #111;
}

.footer__soc.tw:before {
	background-image: url(../img/sprite/sprite.png);
	background-position: -137px -61px;
	width: 17px;
	height: 13px;
}

.footer__soc.fb:before {
	background-image: url(../img/sprite/sprite.png);
	background-position: 0px -109px;
	width: 9px;
	height: 17px;
}

.footer__soc.no:before {
	background-image: url(../img/sprite/sprite.png);
	background-position: -112px -61px;
	width: 15px;
	height: 15px;
}

.footer__soc.pi:before {
	background-image: url(../img/sprite/sprite.png);
	background-position: 0px -61px;
	width: 20px;
	height: 20px;
}

.footer__soc.g:before {
	background-image: url(../img/sprite/sprite.png);
	background-position: -58px -61px;
	width: 19px;
	height: 18px;
}

.footer__soc.dr:before {
	background-image: url(../img/sprite/sprite.png);
	background-position: -30px -61px;
	width: 18px;
	height: 19px;
}

.footer__soc.in:before {
	background-image: url(../img/sprite/sprite.png);
	background-position: -87px -61px;
	width: 15px;
	height: 15px;
}

.footer__soc.m:before {
	background-image: url(../img/sprite/sprite.png);
	background-position: 0px -91px;
	width: 19px;
	height: 8px;
}

.footer__li {
	display: inline-block;
	position: relative;
	padding-right: 0.3125rem;
}

.footer__li:before {
	position: absolute;
	top: 2px;
	left: -5px;
	width: 0.125rem;
	height: 0.875rem;
	background-color: #79797b;
	content: '';
}

.footer__li:first-child:before {
	display: none;
}

.footer__copyright,
.footer__link {
	font-size: 0.875em;
	color: #79797b;
	text-decoration: none;
}

.footer__link {
	transition: .25s linear;
}

.footer__link:hover {
	color: #fff;
}

.footer__copyright {
	display: inline-block;
	margin-bottom: 1.5625rem;
}

.header,
.footer {
	background-color: #e0dfda;
}
